<template>
  <div class="wizard">
    <div class="wizard__bar">
      <div class="wizard__bar__inner">
        <div ref="wizardBar" class="wizard__bar__inner_selected"></div>
      </div>
    </div>

    <div class="wizard__slot-wrap">
      <div
        v-for="(item, i) in steps"
        :key="item.id"
        class="wizard__slot"
        :class="{ wizard__slot_active: modelValue >= i }"
        :style="{ '--color': item.color }"
        @click="setCurrentStep(item, i)"
      >
        <div :ref="el => { wizardCircle[i] = el }" class="wizard__slot__circle">
          <div class="wizard__slot__circle__inner">
          </div>
        </div>
        <span class="wizard__slot__text">{{ item.title }}</span>
      </div>
    </div>

  </div>
</template>

<script>
import { ref, watch, onMounted } from 'vue'
export default {
  name: 'Wizard',
  props: {
    steps: {
      type: Array,
      require: true
    },
    modelValue: {
      type: Number,
      default: 0
    }
  },
  setup (props, { emit }) {
    const wizardBar = ref(null)
    const wizardCircle = ref([])
    const setCurrentStep = (item, i) => {
      emit('update:modelValue', i)
    }
    const changeBarProgress = () => {
      const percent = [85, 50, 0]
      wizardBar.value.style['clip-path'] = 'inset(0 ' + percent[props.modelValue] + '% 0 0)'
    }
    watch(() => props.modelValue, changeBarProgress)
    onMounted(changeBarProgress)
    return {
      wizardBar,
      wizardCircle,
      setCurrentStep
    }
  }
}
</script>

<style lang="scss">
.wizard {
  position: relative;
  margin-bottom: 90px;

  &__bar {
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
    width: 99%;
    height: 22px;
    padding: 4px;

    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.07);
    box-sizing: border-box;
    border-radius: 44px;

    &__inner {
      position: relative;
      height: 100%;

      background: #FFFFFF;
      box-shadow: 0 4px 80px rgba(0, 0, 0, 0.09);
      border-radius: 20px;
      overflow: hidden;

      &_selected {
        transition: clip-path 0.5s;
        height: 100%;
        background: linear-gradient(90deg, #F6E266 0%, #E7B747 100%) left/18% no-repeat,
                    linear-gradient(90deg, #E5AE4F 0%, #D48343 100%) left/50% no-repeat,
                    linear-gradient(90deg, #97BE44 0%, #3AB33F 100%) right/50% no-repeat;
      }
    }
  }
  &__slot-wrap {
    display: flex;
    justify-content: space-around;
    text-align: center;
  }
  &__slot_active .wizard__slot {
    &__text {
      color: map-get($textColors, "black");
    }
    &__circle {
      border: none;
      box-shadow: 0 5px 16px rgba(var(--color), 0.3);

      &__inner {
        display: block;
        height: 100%;
        background: rgba(var(--color), 1);
        border-radius: 50%;
      }
    }
  }
  &__slot {
    position: relative;
    display: flex;
    justify-content: center;

    &__text {
      position: absolute;
      top: calc(100% + 17px);
      width: max-content;

      font-size: 12px;
      line-height: 14px;

      color: map-get($textColors, "grey");
    }
    &__circle {
      background: #fff;
      border-radius: 50%;
      height: 32px;
      width: 32px;
      padding: 8px;
      position: relative;
      overflow: hidden;

      border: 3px solid #F2F2F2;
    }
  }
}
</style>
